@import "../../variables";
.form-header {
  margin: 0 0 40px;

  h1 {
    padding: 4px 0;
    color: $primary-color;
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    width: 100%;
    margin: 0;
  }
}

.authorization-modal {
  overflow: hidden;

  .authorization-modal-content {
    position: relative;
    display: flex;
    transition: transform ease 0.5s, height ease 0.5s;
    height: 100%;
  }


  &.is-sign-in-active {
    height: 353px;

    .authorization-modal-content {
      transform: translateX(0);
    }

    .sign-in-toggle {
      background: $primary-color;
    }

    .register-toggle {
      background: $primary-color;
      color: $secondary-text-color;
    }
  }

  &.is-register-active {
    height: 608px;

    .authorization-modal-content {
      transform: translateX(-100%);
    }

    .sign-in-toggle {
      background: $secondary-text-color;
    }

    .register-toggle {
      background: $secondary-text-color;
    }
  }

  &.is-confirmation-code-active {
    height: 330px;
    .authorization-modal-content {
      transform: translateX(-200%);
    }
  }

  .auth-form {
    width: 95%;
    flex-shrink: 0;
    padding: 20px;

    .auth-info {
      margin-bottom: 40px;
    }

    &.full-size {
      width: 100%;
    }
  }

  .auth-toggle {
    cursor: pointer;
    width: 5%;
    text-align: center;
    flex-shrink: 0;
    vertical-align: middle;
    display: flex;
    justify-content: center;
    transition: background-color ease 0.5s;

    span {
      writing-mode: tb-rl;
    }
  }
}
