@import '../../variables';

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);

  .modal-content {
    z-index: 15;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: $secondary-text-color;
    width: 600px;
    max-height: 90vh;
    max-width: 90vw;
    border-radius: 4px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    overflow: auto;
    font-family: 'Roboto', sans-serif;
    transition: height ease-in .5s;
  }
}
