@import "variables";

body {
  margin: 0;
  font-family: 'Roboto', 'Roboto', 'Oxygen', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

* {
  box-sizing: border-box;
}

#root {
  min-height: 100vh;
}

h1 {
  font-family: 'Oswald', 'Roboto Light', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.form-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 0 20px;

  &:last-child {
    margin: 0;
  }

  label {
    display: block;
    margin: 0 0 10px;
    color: rgba(0, 0, 0, 0.6);
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: 0.2em;
    position: relative;

    i {
      color: $accent-color;
      cursor: pointer;

      &:hover {
        span {
          display: block;
        }
      }

      span {
        position: absolute;
        bottom: 120%;
        left: 0;
        background: $accent-color;
        font-family: 'Roboto', sans-serif;
        text-transform: none;
        white-space: nowrap;
        padding: 10px;
        border-radius: 5px;
        font-weight: normal;
        color: $secondary-text-color;
        display: none;
      }
    }

  }

  button {
    outline: none;
    background: $primary-color;
    width: 100%;
    border: 0;
    border-radius: 4px;
    padding: 12px 20px;
    color: $secondary-text-color;
    font-family: inherit;
    font-size: inherit;
    font-weight: 500;
    line-height: inherit;
    text-transform: uppercase;
    cursor: pointer;
  }

  input {
    outline: none;
    display: block;
    background: rgba(0, 0, 0, 0.1);
    width: 100%;
    border: 0;
    border-radius: 4px;
    box-sizing: border-box;
    padding: 12px 20px;
    color: rgba(0, 0, 0, 0.6);
    font-family: inherit;
    font-size: inherit;
    font-weight: 500;
    line-height: inherit;
    transition: 0.3s ease;
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active {
    border: 0;
    -webkit-box-shadow: 0 0 0 30px #e0e0e0 inset !important;
  }
}

